<template>
  <div class="overview">
    <div class="main">
      <div class="header">
        <h1>The Daily News</h1>
        <hr />
      </div>
      <div class="content">
        <div class="loader" v-if="loading">
          <b-spinner label="Spinning"></b-spinner>
        </div>
        <div class="section" v-else>
          <section class="first">
            <div class="tags">
              <h6>Filter by source:</h6>
              <b-badge
                class="tag"
                v-for="(item, index) in news.sources"
                :key="index"
                variant="light"
                @click="selectTag(item)"
                :class="{ selected: item.$$active }"
                >{{ item.name }}</b-badge
              >
            </div>
          </section>
          <section class="left-section">
            <h3>Top Stories</h3>
            <p class="date-today">{{ dateToday }}</p>
            <div class="today-content">
              <div v-if="filteredNews.length < 1">
                <div
                  class="today-news"
                  v-for="(item, index) in news.data"
                  :key="index"
                >
                  <img :src="item.urlToImage" alt="image" />
                  <h5>{{ item.title }}</h5>
                  <p>
                    <span class="author" v-if="item.author !== null"
                      >{{ item.author }}
                      <span style="font-size:15px;">&#9679;</span> </span
                    ><span class="date"> {{ item.$$date }}</span>
                  </p>
                  <a :href="item.url" target="_blank">Read more</a>
                </div>
              </div>
              <div v-else>
                <div
                  class="today-news"
                  v-for="(item, index) in filteredNews"
                  :key="index"
                >
                  <img :src="item.urlToImage" alt="image" />
                  <h5>{{ item.title }}</h5>
                  <p>
                    <span class="author" v-if="item.author !== null"
                      >{{ item.author }}
                      <span style="font-size:15px;">&#9679;</span> </span
                    ><span class="date"> {{ item.$$date }}</span>
                  </p>
                  <a :href="item.url" target="_blank">Read more</a>
                </div>
              </div>
            </div>
          </section>

          <section class="right-section">
            <h5>Yesterday Stories</h5>
            <p class="date-today">{{ dateYesterday }}</p>
            <div class="yesterday">
              <div
                class="yesterday-content"
                v-for="(item, index) in yesterdayNews.data"
                :key="index"
              >
                <img :src="item.urlToImage" alt="image" />
                <div class="card-content">
                  <h6>{{ item.title }}</h6>
                  <p>
                    <span class="author" v-if="item.author !== null"
                      >{{ item.author }}
                      <span style="font-size:15px;">&#9679;</span> </span
                    ><span class="date"> {{ item.$$date }}</span>
                  </p>
                  <a :href="item.url" target="_blank">Read more</a>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios');
import moment from 'moment';

export default {
  name: 'Overview',
  data() {
    return {
      newsApiKey: process.env.VUE_APP_NEWSAPIKEY,
      ipDataKey: process.env.VUE_APP_IPDATAKEY,
      location: null,
      news: [],
      yesterdayNews: [],
      newsTest: [],
      loading: true,
      dateToday: moment().format('MMMM Do YYYY'),
      dateYesterday: moment()
        .subtract(1, 'days')
        .format('MMMM Do YYYY'),
      selectedSources: [],
      filteredNews: [],
    };
  },
  created() {
    this.getNews();
    this.getNewsYesterday();
  },
  methods: {
    init(data) {
      const news = data
        .map((news) => {
          news['$$date'] = moment(news.publishedAt).calendar();
          news['$$active'] = false;
          news.source['$$active'] = false;
          return news;
        })
        .reduce((arr, obj) => {
          arr['data'] = arr['data'] || [];
          arr['sources'] = arr['sources'] || [];
          arr['data'].push(obj);
          arr['sources'].push(obj.source);

          return arr;
        }, {});

      return news;
    },
    getNews() {
      axios
        .get(`https://api.ipdata.co?api-key=${this.ipDataKey}`)
        .then((response) => {
          return axios.get(
            `https://cors-anywhere.herokuapp.com/https://newsapi.org/v2/top-headlines?country=${response.data.country_code}&pageSize=50&sortBy=popularity&apiKey=${this.newsApiKey}`
          );
        })
        .then((response) => {
          this.news = this.init(response.data.articles);

          this.news.sources = this.news.sources.filter(
            (source, index, self) =>
              self.findIndex((s) => s.name === source.name) === index
          );

          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    getNewsYesterday() {
      const yesterdayDate = moment().format('YYYY-MM-DD');
      axios
        .get(`https://api.ipdata.co?api-key=${this.ipDataKey}`)
        .then((response) => {
          return axios.get(
            `https://cors-anywhere.herokuapp.com/https://newsapi.org/v2/everything?q=${response.data.country_name}&pageSize=50&sortBy=popularity&from=${yesterdayDate}&to=${yesterdayDate}&apiKey=${this.newsApiKey}`
          );
        })
        .then((response) => {
          this.yesterdayNews = this.init(response.data.articles);
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    selectTag(value) {
      const selectedSources = this.news.sources.map((source) => {
        if (value.name === source.name) {
          source.$$active = !source.$$active;
        }
        return source;
      });

      this.news.sources = selectedSources;

      const news = this.news.data.map((news) => {
        if (value.name === news.source.name) {
          news.$$active = !news.$$active;
        }
        return news;
      });

      this.filteredNews = news.filter((news) => {
        return news.$$active === true;
      });
    },
  },
};
</script>

